<template>
  <div>
    <el-card shadow='hover' class='card'>
      <el-divider class='divider' content-position="right" @click='closeAlertDetail'>
        <div class='back'>
          <el-image class='backIcon' :src="require('@/../public/assets/return.png')"></el-image>
          <span class='backWord'>{{$t('General.Return')}}</span>
        </div>
      </el-divider>

      <div class='content'>
        <div class='type item'>
          <span class='left'>{{typeFormat}}</span>
        </div>

        <div class='time item'>
          <span class='left'>{{$t('Alert.DetailTime')}}</span>
          <span class='right'>{{time}}</span>
        </div>

        <div class='user item'>
          <span class='left'>{{$t('Alert.DetailFrom')}}</span>
          <span class='right'>{{username}} ({{realname}} - {{studentid}})</span>
        </div>

        <div class='location item'>
          <span class='left'>
            <span>{{$t('Alert.DetailLocation')}}</span>
          </span>
          <span class='right'>
            <span>{{location}}</span>
          </span>
        </div>
      </div>

      <div class='button'>
        <el-button class='button' type='primary' v-if='type==4&&!end'
          @click='place()'>{{$t('Alert.DetailPlace')}}</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'alert-detail',
  data () {
    return {
      alertid: 0,
      time: '',
      type: 0,
      typeFormat: '',
      userid: 0,
      username: '',
      realname: '',
      studentid: 0,
      itemname: '',
      location: '',
      targetid: 0,
      end: true
    }
  },
  methods: {
    getAlert () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.getAlertItem,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginAs'),
          'alertid': this.alertid
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'ALERT_GETITEM_COMPLETE') {
            this.type = response.data.item.type;
            this.time = this.formatTime(response.data.item.time);
            this.userid = response.data.item.userid;
            this.username = JSON.parse(response.data.item.content).username;
            this.realname = JSON.parse(response.data.item.content).realname;
            this.studentid = JSON.parse(response.data.item.content).studentid;
            this.itemname = JSON.parse(response.data.item.content).itemname || JSON.parse(response.data.item.content).itemname;
            this.location = JSON.parse(response.data.item.content).location;
            this.typeFormat = this.typeFormatter(this.type)
            this.targetid = JSON.parse(response.data.item.content).userid;
            this.end = JSON.parse(response.data.item.content).end || false;
          }
          else {
            this.notfound();
          }
        }
      })
    },
    typeFormatter (type) {
      if (type == 4) {
        return this.$t('Alert.DetailType4')
      }
      else if (type == 5) {
        return this.$t('Alert.DetailType5')
      }
      else if (type == 6) {
        return this.$t('Alert.DetailType6')
      }
    },
    closeAlertDetail () {
      this.$bus.$emit('showAlertList');
    },
    place () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.purchaseMarketPlace,
        data: new URLSearchParams({
          'newalertid': nanoid(),
          'cookie': Cookies.get('loginAs'),
          'alertid': this.alertid,
          'targetid': this.targetid,
          'itemname': this.itemname,
          'location': this.location
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'MARKET_PLACE_ALERTIDTAKEN') {
            this.place();
            return;
          }
          else if (response.data == 'MARKET_PLACE_COMPLETE') {
            this.alert(this.$t('Alert.DetailReplySend'), this.$t('System.Info'))
          }
        }
      })
    }
  },
  mounted () {
    this.alertid = this.$route.params.id
    this.getAlert();
  }
}
</script>

<style scoped>
.card {
  width: 90%;
  margin-left: 5%;
  border-radius: 15px;
  padding-top: 20px;
  min-height: 800px;
}
.divider {
  width: 90%;
  margin-left: 5%;
}
.back {
  cursor: pointer;
}
.backIcon {
  color: rgb(252, 62, 5);
  width: 20px;
}
.backWord {
  font-size: 18px;
  margin-left: 10px;
}
.content {
  text-align: left;
  font-size: 20px;
  width: 90%;
  margin-left: 5%;
}
.right {
  float: right;
}
.type {
  font-size: 25px;
  margin-bottom: 30px;
}
.item {
  margin-top: 20px;
}
.button {
  margin-top: 30px;
  font-size: 20px;
  border-radius: 15px;
}
</style>